<script>
import store from '@/store'

import BookItem from '@/components/BookItem.vue';
import BackButton from '@/components/layout/BackButton.vue';
import LoadingQuote from '@/components/LoadingQuote.vue';
import Header from '@/components/layout/Header';

// import BookAdaptationItem from '@/components/bookpagecomponents/BookAdaptationItem.vue';
// import BookQuoteItem from '@/components/bookpagecomponents/BookQuoteItem.vue';

import { TimelineLite, Sine, gsap, CSSPlugin } from 'gsap'

gsap.registerPlugin(CSSPlugin);




export default {



    name: "BookPage",
    components: {
        BookItem,
        BackButton,
        LoadingQuote,
        Header,
        // BookSummaryItem,
        // BookAdaptationItem
    },
    data(){
        return{
            expandedSummary: true,
            expandedDownloads: true,
            expandedAdaptations: true,
            selectedMovie: false,
            adaptations: store.adaptations,
            selectedIndex: 0,
            fileName: this.slug,
            isLoading:true,
        }
    },
    props:{
        slug: {
            type: String,
            required: true
        },
        adaptation: {
            type: Object,
        },
        selectedFilm: {
            type: Number,
        }
    },
  
    methods: {
          selectMovie(index){
            this.selectedIndex = index;
             console.log(index) 
          },
        next(){
            this.selectedIndex++
        },
        prev(){
            this.selectedIndex--
        },
        onLoad: function(){
            console.log("call on load");
            this.isLoading = false;
        },
    },
    created: function(){
        this.onLoad();
    },
    computed: {
        book(){
            return store.books.find(
                book => book.slug === this.slug
            )
        },
    },
    mounted() {
      const { bookAnimation } = this.$refs;
      const timelineBook = new TimelineLite();

      timelineBook.to(bookAnimation, 1, {
           y: -500,
           x: 200,
           ease: Sine.easeIn,
        });

    //   setTimeout(() => {
    //     this.isLoading = false
    //   }, 5000)
    },
}  

</script>


